// import Fuse from "fuse.js"; //* feature search
// import _ from "lodash";
import SeatApi from "@/api/fake/seat.js";
// import { isEmptyObject, replaceItem, removeItem, formatSlug } from "@/utils";
import { TABLE } from "@/utils/constants";

const table = TABLE.SEAT;

//default state
const initialState = () => ({
  // all: [], // all data wrapper of list
  // search: [], // data search //* feature search
  list: [], // all data
  loading: false
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  // all: state => state.all,
  // search: state => state.search
  list: state => state.list,
  loading: state => state.loading
};

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET(state, data) {
    state.list = data;
  },

  CLEAN(state) {
    state.list = [];
  },

  //reset state
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key];
    });
  }
};
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setItem: ({ commit }, val) => {
    commit("SET_ITEM", val);
  },

  setup: async ({ commit, state }, opt = {}) => {
    state.loading = true; //set loading
    opt.url = table;
    let result = await SeatApi.getAll(opt);
    if (result.data) {
      if (result.data.components) result = result.data.components;
      else {
        result = result.data;
      }
    }
    console.log(result);
    state.loading = false; //set loading
    commit("SET", result || []);
  },

  find: async ({ commit, state }, opt = {}) => {
    state.loadingItem = true; //set loading

    let result = state.list.find(val => val[opt.key] === opt.value);

    state.loadingItem = false; //set loading
    commit("SET_ITEM", result || {});
    return result;
  },

  clean: async ({ commit }) => {
    commit("CLEAN");
  },

  reset: async ({ commit }) => {
    commit("RESET");
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
