<template>
  <div class="menu-page page">
    <template v-if="listByType && listByType.length">
      <div class="container">
        <div class="row row-space-around">
          <div class="col col-12">
            <div class="bg-white">
              <van-checkbox-group
                v-model="checkboxMenu"
                direction="horizontal"
                ref="cboxs"
                class="bg-content"
              >
                <van-checkbox name="all" @click="toggleAll"
                  >Select All</van-checkbox
                >
                <van-checkbox
                  v-for="(pt, k) in listByType"
                  :name="pt.type"
                  :key="k + pt.type"
                  :ref="'cbox-' + pt.slug"
                >
                  {{ pt.type }}
                </van-checkbox>
              </van-checkbox-group>
            </div>
          </div>
        </div>
        <template v-for="(pt, k) in listByType" :title="pt.truncate">
          <template v-for="(c, ck) in checkboxMenu">
            <div
              class="row row-space-around"
              v-if="pt.type === c"
              :key="k + ck"
            >
              <div class="col col-12">
                <h3 class="design">
                  <span>{{ pt.type }}</span>
                </h3>
              </div>
              <div
                class="col col-large-4 col-medium-6 col-small-12"
                v-for="(p, i) in pt.data"
                :key="i"
              >
                <product :item="p" @clicked="onClicked" />
              </div>
            </div>
          </template>
        </template>
      </div>
    </template>

    <van-popup v-model="popup" class="vpopup" closeable>
      <template v-if="item">
        <product :item="item" class="vertical" :more="true" />
      </template>
    </van-popup>

    <loader v-if="loading" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import Product from "@/components/Product.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: "menu-page",
  components: {
    Product,
    Loader
  },
  data() {
    return {
      tabs: 0,
      popup: false,
      checkboxToggle: false,
      checkboxMenu: []
    };
  },
  computed: {
    ...mapGetters("Product", {
      list: "list",
      listByType: "listByType",
      item: "item",
      loading: "loading",
      loadingItem: "loadingItem"
    })
  },
  methods: {
    ...mapActions("Product", {
      setItem: "setItem",
      setup: "setup"
    }),
    onClicked(data) {
      let vm = this;
      vm.setItem(data);
      vm.popup = true;
    },
    toggleAll() {
      let vm = this;
      vm.checkboxToggle = !vm.checkboxToggle;
      vm.$refs.cboxs.toggleAll(vm.checkboxToggle);
    }
  },
  async mounted() {
    let vm = this;
    if (!vm.list.length) {
      await vm.setup();
    }
    vm.type = vm.$route.query.type;
    if (vm.type) {
      const cbox = vm.$refs["cbox-" + vm.type];
      if (cbox) cbox[0].toggle();
    } else {
      vm.toggleAll();
    }
  }
};
</script>
