import Storage from "@/utils/storage";
import PaymentApi from "@/api/real/payment.js";
// import Fuse from "fuse.js"; //* feature search
// import { isEmptyObject, isEmptyArray, replaceItem, removeItem } from "@/utils";
/**
 * get local storage cart
 */
const nameStore = "fc_Card";

//default state
const initialState = () => ({
  // all: [], // all data wrapper of list
  // search: [], // data search //* feature search
  history: [], // cart paymented
  list: Storage.get(nameStore, "[]"), // all data
  item: {}, // data select (detail, edit)
  size: 0,
  loading: false,
  loadingItem: false,
  alert: ""
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  // all: state => state.all,
  // search: state => state.search
  list: state => state.list,
  size: state => {
    let size = 0;
    state.list.forEach(function(item) {
      if (item.quantity) {
        size += item.quantity;
      }
    });
    return size;
  },
  item: state => state.item,
  loading: state => state.loading,
  loadingItem: state => state.loadingItem,
  totalAmount: state => {
    // reduce : total gi tr ban u = 0 , lp cng dn cho ti cui mng
    return state.list.reduce((total, record) => {
      return total + record.price * record.quantity;
    }, 0);
  }
};

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET(state, data) {
    state.list = data;
    // set data
    Storage.set(nameStore, JSON.stringify(state.list), 60 * 24); //1 day
  },

  SET_ITEM(state, data) {
    state.item = data;
  },

  SET_ALERT(state, data) {
    state.alert = data;
  },

  ADD(state, data) {
    // kim tra cart
    let cart = state.list.find(record => record.id === data.id);

    if (cart) {
      // sp c sn th tng s lng
      // if ht trong kho
      if (cart.quantity >= cart.stock) {
        state.alert = "max quantity";
        return;
      }
      cart.quantity++;
    } else {
      // sp mi th cho vo gi
      state.list.push({
        ...data,
        stock: data.quantity, // s lng gc cn trong kho
        quantity: 1
      });
    }

    // set data
    Storage.set(nameStore, JSON.stringify(state.list), 60 * 24); //1 day
  },

  REMOVE(state, data) {
    let cart = state.list.find(record => record.id === data.id);
    if (cart.quantity < 1) return;
    // gim s lng
    cart.quantity--;
    // set data
    Storage.set(nameStore, JSON.stringify(state.list), 60 * 24); //1 day
  },
  REMOVE_ALL(state, data) {
    state.list.splice(state.list.indexOf(data), 1);
    // set data
    Storage.set(nameStore, JSON.stringify(state.list), 60 * 24); //1 day
  },

  CLEAN(state) {
    state.list = [];
    // set data
    Storage.remove(nameStore);
  },

  //reset state
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key];
    });
  }

  //* feature search
  // SEARCH(state, data) {
  //   const options = {
  //     shouldSort: true,
  //     threshold: 0.5,
  //     location: 0,
  //     distance: 50, //Xc nh mc  trng khp phi  v tr tm thy k t
  //     minMatchCharLength: 2,
  //     findAllMatches: true,
  //     keys: ["title", "description"]
  //   };

  //   const fuse = new Fuse(state.all, options);

  //   //extend search join 2 char 'Man 'Old
  //   let results = fuse.search(data);
  //   if (results && results.length) {
  //     let output = [];
  //     for (const it of results) {
  //       output.push(it.item);
  //     }
  //     state.search = output;
  //   } else {
  //     state.search = [];
  //   }
  // },
};
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setItem: ({ commit }, val) => {
    commit("SET_ITEM", val);
  },

  setup: async ({ commit, state }) => {
    state.loading = true; //set loading
    //get cart from store
    let result;
    state.loading = false; //set loading
    commit("SET", result || []);
  },

  find: async ({ commit, state }, opt = {}) => {
    state.loadingItem = true; //set loading

    let result = state.list.find(val => val[opt.key] === opt.value);

    state.loadingItem = false; //set loading
    commit("SET_ITEM", result || {});
    return result;
  },

  add: async ({ commit, state }, data) => {
    state.loading = true; //set loading

    //thm 1 sp vo gi
    commit("ADD", data);
    //gim 1 sp trong kho
    commit("Product/INCREMENT_QUANTITY", data, { root: true });

    state.loading = false; //set loading
  },

  remove: async ({ commit, state }, data) => {
    state.loading = true; //set loading

    // Gim 1 sp trong gi
    commit("REMOVE", data);

    // Tng 1 sp trong kho
    commit("Product/DECREMENT_QUANTITY", data, { root: true });

    state.loading = false; //set loading
  },

  removeAll: async ({ commit, state }, data) => {
    state.loading = true; //set loading

    // xo ht sp ca 1 mt hng trong gi
    commit("REMOVE_ALL", data);

    // Cp nht li s lng ban u ca sp
    commit("Product/INTIAL_QUANTITY", data, { root: true });

    state.loading = false; //set loading
  },

  clean: async ({ commit }) => {
    commit("CLEAN");
  },

  reset: async ({ commit }) => {
    commit("RESET");
  },

  payment: async ({ commit }, opt) => {
    let result = await PaymentApi.add(opt);
    let output = result.data;
    if (output && output.success) {
      commit("CLEAN");
      return true;
    } else {
      return false;
    }
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
