/**
 * Doc https://day.js.org/
 * Use $dayjs or $date
 * JS: this.$date('2018-08-08').format('DD/MM/YYYY');
 * HTML: <span> {{ $date('2018-08-08').format('DD/MM/YYYY') }} </span>
 * // $date.locale('fr') // switch back to default France locale globally
 */
const dayjs = require("dayjs");
// require('dayjs/locale/fr'); //import france

export default {
  /**
   * Vue
   * @param {Vue} Vue
   */
  install: function(Vue) {
    Object.defineProperties(Vue.prototype, {
      $dayjs: {
        get: function() {
          return dayjs;
        }
      },
      $date: {
        get: function() {
          return dayjs;
        }
      }
    });
    Vue.dayjs = dayjs;
  }
};
