<template>
  <div class="cart-page page">
    <div class="container">
      <h2 class="title design">
        <span>Cart</span>
      </h2>

      <div class="cart-show">
        <div class="row" v-if="list.length">
          <div
            class="col col-small-12 col-medium-6 col-large-4"
            v-for="(c, i) in list"
            :key="i"
          >
            <product :item="c" :cart="true" />
          </div>
        </div>
        <div class="row row-space-around" v-else>
          <div class="col col-12">
            <div class="bg-white">
              <div class="bg-content">Empty !!!</div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <div class="pay design">
              <span class="price">
                <span class="price--name">Provisional Amount: </span>
                <span class="price--currency">$</span>
                <span class="price--number">{{ currency(totalAmount) }}</span>
              </span>
              <button class="pay-submit nut" @click="onSubmit">Payment</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-dialog
      v-model="billShow"
      class="vpopup vbill"
      closeable
      show-cancel-button
      show-confirm-button
      :close-on-click-overlay="false"
      :before-close="billAction"
    >
      <Bill
        :items="list"
        :code="code"
        :total="totalAmount"
        :timestamp="timestamp"
      />
    </van-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters, mapActions } from "vuex";

import { uuid } from "@/utils";
import Product from "@/components/Product.vue";
import Bill from "@/components/Bill/Main.vue";

export default {
  name: "cart",
  components: {
    Product,
    Bill
  },
  data() {
    return {
      billShow: false,
      currentContact: {
        name: "John Snow",
        tel: "13000000000"
      },
      code: null,
      timestamp: null
    };
  },
  computed: {
    ...mapGetters("Cart", {
      list: "list",
      item: "item",
      loading: "loading",
      loadingItem: "loadingItem",
      totalAmount: "totalAmount"
    }),
    ...mapGetters("Product", {
      productList: "list"
    })
  },
  methods: {
    ...mapActions("Product", {
      productSetup: "setup"
    }),
    ...mapActions("Cart", {
      payment: "payment"
    }),
    onSubmit: function() {
      console.log("Payment");
      this.billShow = true;
      this.code = uuid();
      this.timestamp = this.$date().format("DD/MM/YYYY, HH:mm:ss");
    },
    async billAction(action, done) {
      if (action === "confirm") {
        // this.$refs.profileRef.onSubmit()
        // setTimeout(done, 1000);
        let output = {
          id: this.code,
          timestamp: this.timestamp,
          info: JSON.stringify(this.list),
          total: this.currency(this.totalAmount)
        };
        let result = await this.payment(output);
        if (result) {
          this.$toast.success(`Payment success`);
        } else {
          this.$toast.success(`Payment failed`);
        }
        done();
      } else {
        done();
      }
    }
  },
  created() {
    let vm = this;
    if (!vm.productList.length) {
      vm.productSetup();
    }
  }
};
</script>
