<template>
  <component v-bind:is="layout" v-if="loaded" />
</template>

<script>
import Default from "@/layouts/Default.vue";
import Empty from "@/layouts/Empty.vue";

export default {
  name: "App",
  components: {
    Default,
    Empty
  },
  data() {
    return {
      loaded: false //router loaded
    };
  },
  watch: {
    $route: function() {
      let vm = this;
      vm.$nextTick(() => (vm.loaded = true)); //router loaded
    }
  },
  computed: {
    layout() {
      return this.$route.meta.layout || "default"; //set layout
    }
  },
  mounted() {
    if (this.$route.name && this.$route.name != "login") this.loaded = true; //router loaded
  }
};
</script>
