<template>
  <div>
    <div class="vcard" :class="{ off: !item.status }" @click="onClick()">
      <div class="vcard-head">
        <div class="vcard-pic">
          <img class="vcard-img" :src="item.pic" />
        </div>
      </div>
      <div class="vcard-body">
        <h3 class="vcard-title">
          {{ item.name }}
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
export default {
  name: "Card",
  props: ["item"],
  methods: {
    onClick() {
      let vm = this;
      let type = vm.item.status ? "warning" : "success";
      let temp = vm.item.status ? "full" : "empty";
      let messager = `Seat '${vm.item.name}' is ${temp}`;
      vm.item.status = !vm.item.status;
      Notify({ type: type, message: messager });
    }
  }
};
</script>
