  text-align: center;
  overflow: hidden;
  position: relative;
  display: block;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  margin-bottom: 1rem;
  box-shadow: 0px 3px 0px 0px rgba(0, 0, 0, 0.3);
}
