/**
 * use api google sheet
 */
import { SHEET_API } from "@/utils/constants";
import axios from "axios";

const Api = opts => {
  let params = {
    action: opts.action || "read",
    table: opts.table || "list"
  };
  params.data = opts.data;

  // console.log(params);
  return axios({
    method: "post",
    url: opts.url || SHEET_API.PAYMENT,
    crossDomain: true,
    params: params
  });
};

export default {
  getAll: function(params) {
    // params.data is require
    // no id is getAll
    return Api({
      action: "read",
      table: "list",
      data: params
    });
  },
  get: function(params) {
    // params.data is require
    // no id is getAll
    return Api({
      action: "read",
      table: "list",
      data: params
    });
  },
  add: function(params) {
    // params.id is require
    return Api({
      action: "insert",
      table: "list",
      data: params
    });
  },
  edit: function(params) {
    return Api({
      action: "read",
      table: "list",
      data: params
    });
  },
  remove: function(params) {
    return Api({
      action: "read",
      table: "list",
      data: params
    });
  }
};
