import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";

import "@/plugins/validate";
import date from "@/plugins/date";
Vue.use(date);
import mixin from "@/plugins/mixin";
Vue.mixin(mixin);

//toast
import AWN from "awesome-notifications";
import "@/assets/css/awn.css";
Vue.prototype.$toast = new AWN({
  position: "bottom-right",
  icons: {
    enabled: true,
    prefix: "<i class='van-icon van-icon-",
    suffix: "'></i>",
    tip: "question-o",
    info: "info-o",
    success: "certificate",
    warning: "warning-o",
    alert: "warn-o",
    async: "setting-o",
    confirm: "warn-o"
  }
});

//framework ui
import "@/assets/css/the-flex-grid.css";
import "vant/lib/index.css";
import Vant, { Locale, Icon } from "vant";
import enUS from "vant/lib/locale/lang/en-US";
Locale.use("en-US", enUS);
Vue.use(Icon);
Vue.use(Vant);

import VueLazyload from "vue-lazyload";
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: "img/loader.svg",
  loading: "img/loader.svg",
  attempt: 1
});

//slider
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "@/assets/css/slick.scss";
Vue.component("VueSlickCarousel", VueSlickCarousel);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
