export const uuid = function() {
  return "fc-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
    var r = (Math.random() * 16) | 0,
      v = c == "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};
export const isEmptyObject = function(obj) {
  return Object.keys(obj).length === 0 && obj.constructor === Object;
};

export const isEmptyArray = function(t) {
  if (t.constructor === Array && t.length) return true;
  return;
};

export const isJsonString = function(s) {
  try {
    JSON.parse(s);
  } catch (e) {
    return false;
  }
  return true;
};

export const debounce = function(fn, wait) {
  let t;
  return function() {
    clearTimeout(t);
    t = setTimeout(() => fn.apply(this, arguments), wait);
  };
};

export const sleep = function(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
};

// plus https://stackoverflow.com/a/54744145
export const formatSlug = function(str) {
  try {
    return str
      .toLowerCase()
      .replace(/[]/g, "a")
      .replace(/[]/g, "e")
      .replace(/[]/g, "d")
      .replace(/[]/g, "i")
      .replace(/[]/g, "o")
      .replace(/[]/g, "u")
      .replace(/[]/g, "y")
      .replace(/[^\w\s]/gi, "") //replace special chars
      .replace(/  +/g, " ") //replace multi space to one space
      .replace(/^ +/, "") // Trim " " from start of text
      .replace(/ +$/, "") // Trim " " from end of text
      .replace(/ /g, "-");
  } catch {
    return "";
  }
};

export const urlParse = function(str) {
  let query = "";
  let href = str || location.href;
  const url = href.split("?");
  if (url.length >= 2) {
    query = url[1];
  }

  let result = {};
  query.split("&").forEach(function(part) {
    if (part) {
      var item = part.split("=");
      result[item[0]] = decodeURIComponent(item[1]);
    }
  });
  return result;
};

export const urlStringify = function(obj) {
  var encode = encodeURIComponent;
  return Object.keys(obj || {})
    .reduce(function(arr, key) {
      [].concat(obj[key]).forEach(function(v) {
        arr.push(encode(key) + "=" + encode(v));
      });
      return arr;
    }, [])
    .join("&")
    .replace(/\s/g, "+");
};

export const groupBy = function(xs, f) {
  return xs.reduce(function(r, v) {
    var k =
      arguments.length <= 4 || arguments[4] === undefined ? f(v) : arguments[4];
    return (r[k] || (r[k] = [])).push(v), r;
  }, {});
};

//replaceObjectInArray
export const replaceItem = function(arr, obj, key) {
  let index = arr.findIndex(val => val[key] === obj[key]);
  if (index === -1) {
    arr.push(obj);
  } else {
    arr[index] = Object.assign(arr[index], obj);
  }
  return arr;
};

export const removeItem = function(arr, data) {
  return arr.splice(arr.indexOf(data), 1);
};

export const removeByKey = function(arr, key, value) {
  return arr.filter(val => val[key] !== value);
};

export function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}
