// import Fuse from "fuse.js"; //* feature search
import Storage from "@/utils/storage";
const { atob, btoa } = require("abab");
const after_hash = "09gnud==";

//default state
const initialState = () => ({
  item: {
    email: "",
    token: "",
    profile: {}
  }, // data select (detail, edit)
  loading: false,
  loadingItem: false
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  item: state => state.item,
  loading: state => state.loading,
  loadingItem: state => state.loadingItem
};

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET_ITEM(state, data) {
    let _temp = btoa(JSON.stringify(data)) + after_hash;
    Storage.set("authMiddle", _temp, 60 * 24 * 3); //3 day
    state.item = data;
  },

  CLEAN(state) {
    state.item = {};
    Storage.remove("authMiddle");
  },

  //reset state
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key];
    });
  }
};
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setItem: async ({ commit }, opt) => {
    commit("SET_ITEM", opt || {});
  },

  compute: async ({ commit, state }) => {
    if (!state.item.email) {
      const str = Storage.get("authMiddle");
      if (str) {
        let _temp = str.replace(after_hash, "");
        let opt = atob(_temp);
        commit("SET_ITEM", JSON.parse(opt));
      }
    }
  },

  clean: async ({ commit }) => {
    commit("CLEAN");
  },

  reset: async ({ commit }) => {
    commit("RESET");
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
