<template>
  <canvas class="qr"></canvas>
</template>
<script>
import QRious from "qrious";
export default {
  name: "qr",
  props: ["size"],
  mounted() {
    new QRious({
      element: this.$el,
      value: "https://flashcoffee.github.io/",
      size: this.size || 120
    });
  }
};
</script>
<style lang="scss">
.qr {
  display: block;
  margin: 0 auto 0.5rem;
}
</style>
