<template>
  <div class="seats-page page">
    <div class="container">
      <h2 class="title design" style="font-size: 1.1em;">
        <span>seats</span>
      </h2>

      <div class="seats-show" v-if="list.length">
        <div class="row">
          <div
            class="col col-6 col-medium-4 col-large-3"
            v-for="(p, i) in list"
            :key="i"
          >
            <card :item="p" ref="seat" />
          </div>
        </div>
      </div>
    </div>
    <loader v-if="loading" />
  </div>
</template>

<script>
import { setInterval } from "timers";
import { getRandomInt } from "@/utils";
import { mapActions, mapGetters } from "vuex";
import Card from "@/components/Card.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: "Seats",
  components: {
    Card,
    Loader
  },
  computed: {
    ...mapGetters("Seat", {
      list: "list",
      loading: "loading"
    })
  },
  methods: {
    ...mapActions("Seat", {
      setup: "setup"
    })
  },
  async mounted() {
    let vm = this;
    if (!vm.list.length) {
      await vm.setup();
    }

    setInterval(() => {
      if (vm.list.length) {
        let index = getRandomInt(0, vm.list.length - 1);
        if (vm.$refs["seat"][index]) vm.$refs["seat"][index].onClick();
      }
    }, 5000);
  }
};
</script>
