<template>
  <div class="vbox">
    <div class="vbox-head">
      <div class="vbox-pic">
        <img class="vbox-img" :src="item.pic" @click="next(item)" />
      </div>
      <div class="vbox-tag" v-if="item.tag">{{ item.tag }}</div>
    </div>
    <div class="vbox-body">
      <h3 class="vbox-title" @click="next(item)">
        {{ item.title }}
      </h3>
      <template v-if="!cart">
        <div class="vbox-description" v-if="item.description">
          {{ item.description }}
        </div>
      </template>
      <div class="vbox-money">
        <span class="vbox-currency">$</span>
        <span class="vbox-price">{{ item.price }}</span>
        <span class="vbox-sale">{{ item.price * item.sale + item.price }}</span>
        <span class="vbox-tick" v-if="cart">type</span>
      </div>
      <van-stepper
        v-model="item.quantity"
        :max="item.stock"
        theme="round"
        button-size="22"
        v-if="cart"
        @plus="onAdd(item)"
        @minus="onRemove(item)"
      />
      <van-rate
        v-if="rate"
        v-model="item.rating"
        allow-half
        void-icon="star"
        void-color="#eee"
      />
      <div class="vbox-more" v-if="more">
        <div class="vbox-more-item" v-if="sizeArr.length">
          <div class="vbox-more-title">Size</div>
          <van-radio-group v-model="sizeOpt">
            <van-cell-group>
              <van-cell
                :title="val"
                clickable
                @click="sizeOpt = val"
                v-for="(val, key) in sizeArr"
                :key="key"
              >
                <template #right-icon>
                  <van-radio :name="val" />
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
        <div class="vbox-more-item" v-if="sugarArr.length">
          <div class="vbox-more-title">Sugar</div>
          <van-radio-group v-model="sugarOpt">
            <van-cell-group>
              <van-cell
                :title="val"
                clickable
                @click="sugarOpt = val"
                v-for="(val, key) in sugarArr"
                :key="key"
              >
                <template #right-icon>
                  <van-radio :name="val" />
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
      </div>
    </div>
    <div class="vbox-footer" v-if="cart">
      <button class="vbox-remove ripple" @click="onRemoveAll(item)">
        Remove All
      </button>
    </div>
    <div class="vbox-footer" v-else>
      <button class="vbox-like ripple">Pin</button>
      <button class="vbox-cart ripple" @click="onAdd(item)">Add</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Product",
  props: ["item", "cart", "rate", "more"],
  data() {
    return {
      rating: 0,
      quantity: 0,
      step: 0,
      sugarOpt: "medium",
      sugarArr: [],
      sizeOpt: "medium",
      sizeArr: []
    };
  },
  watch: {
    item: {
      immediate: true,
      handler(val) {
        if (val) {
          let sugar = val.sugar;
          if (sugar) this.sugarArr = sugar.split("|");

          let size = val.size;
          if (size) this.sizeArr = size.split("|");
        }
      }
    }
  },
  methods: {
    ...mapActions("Cart", {
      add: "add",
      remove: "remove",
      removeAll: "removeAll"
    }),
    onFlying() {
      let iCart = document.querySelector("#i-cart");
      if (iCart) {
        iCart.classList.add("flying");
        iCart.addEventListener("animationend", () => {
          iCart.classList.remove("flying");
        });
      }
    },
    onAdd(data) {
      let out = { ...data };
      out.size = this.sizeOpt;
      out.sugar = this.sugarOpt;
      this.add(out);
      this.onFlying();
    },
    onRemove(data) {
      this.remove(data);
      this.onFlying();
    },
    onRemoveAll(data) {
      this.removeAll(data);
      this.onFlying();
    },
    next(data) {
      this.$emit("clicked", data);
    }
  }
};
</script>
