<template>
  <div class="home-page page">
    <div class="container">
      <div class="row">
        <div class="col col-12">
          <vue-slick-carousel
            class="slider-top"
            v-bind="sliderOpt"
            v-if="list && list.length"
          >
            <template v-for="(val, index) in list">
              <div class="slide" v-if="index < 6" :key="index">
                <img v-lazy="val.pic" />
                <div class="design">{{ val.title }}</div>
              </div>
            </template>
          </vue-slick-carousel>
        </div>
      </div>
    </div>

    <van-tabs
      v-model="tabs"
      scrollspy
      sticky
      v-if="listByType && listByType.length"
    >
      <div class="container">
        <div class="row">
          <van-tab
            class="col col-12 col-medium-6 col-large-4"
            v-for="(pt, k) in listByType"
            :title="pt.truncate"
            :key="k"
          >
            <div class="vbox-wrap">
              <h3 class="link design" @click="redirectMenu(pt.slug)">
                <span>{{ pt.type }}</span>
              </h3>
              <template v-for="(d, dk) in pt.data">
                <product
                  :item="d"
                  @clicked="onClicked"
                  :key="dk"
                  v-if="dk < 4"
                />
                <p class="text-right" v-else-if="dk == 4" :key="dk">
                  <a @click="redirectMenu(pt.slug)"> More &rarr; </a>
                </p>
              </template>
            </div>
          </van-tab>
        </div>
      </div>
    </van-tabs>

    <van-popup v-model="popup" class="vpopup" closeable>
      <template v-if="item">
        <product :item="item" class="vertical" :more="true" />
      </template>
    </van-popup>

    <loader v-if="loading" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import Product from "@/components/Product.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: "home-page",
  components: {
    Product,
    Loader
  },
  data() {
    return {
      tabs: 0,
      popup: false,
      sliderOpt: {
        infinite: true,
        slidesToShow: 5,
        slidesToScroll: 2,
        speed: 300,
        autoplay: true,
        autoplaySpeed: 3000,
        arrows: false,
        dots: true,
        adaptiveHeight: true,
        draggable: true,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 4,
              slidesToScroll: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      }
    };
  },
  computed: {
    ...mapGetters("Product", {
      list: "list",
      listByType: "listByType",
      item: "item",
      loading: "loading",
      loadingItem: "loadingItem"
    })
  },
  methods: {
    ...mapActions("Product", {
      setItem: "setItem",
      setup: "setup"
    }),
    onClicked(data) {
      let vm = this;
      vm.setItem(data);
      vm.popup = true;
    },
    redirectMenu(slug) {
      return this.$router.push("/menu?type=" + slug);
    }
  },
  created() {
    let vm = this;
    if (!vm.list.length) {
      vm.setup();
    }
  }
};
</script>
