/**
 * use wrapper axios
 */
const list = require("./seat.json");
export default {
  getAll: function(opt) {
    console.log(opt);
    return new Promise(resolve => resolve({ data: list }));
  },
  get: function(opt) {
    let out = list.find(item => item.id == opt.id);
    console.log(out);
    return new Promise(resolve => resolve({ data: out }));
  },
  add: function(opt) {
    return new Promise(resolve => resolve({ data: opt }));
  },
  edit: function(opt) {
    let out = list.find(item => item.id == opt.id);
    return new Promise(resolve => resolve({ data: out }));
  },
  remove: function(opt) {
    let out = list.find(item => item.id == opt.id);
    return new Promise(resolve => resolve({ data: out }));
  }
};
