<template>
  <div id="app">
    <van-tabbar route style="z-index:8;">
      <van-tabbar-item replace to="/" icon="wap-home-o"> Home </van-tabbar-item>
      <van-tabbar-item replace to="/menu" icon="orders-o">
        Menu
      </van-tabbar-item>
      <van-tabbar-item
        id="i-cart"
        replace
        to="/cart"
        icon="cart-o"
        :badge="this.$store.getters['Cart/size']"
      >
        Card
      </van-tabbar-item>
      <van-tabbar-item replace to="/seats" icon="location-o">
        Seats
      </van-tabbar-item>
      <!-- <van-tabbar-item replace to="/user" icon="user-o">
        User
      </van-tabbar-item> -->
    </van-tabbar>

    <div id="app-title">
      <h1><span>Flash</span> Coffee</h1>
    </div>

    <transition name="fade" mode="out-in">
      <router-view />
    </transition>
  </div>
</template>
<script>
import { Notify } from "vant";
export default {
  computed: {
    alert: {
      get() {
        return this.$store.state.Cart.alert;
      },
      set(val) {
        this.$store.commit("Cart/SET_ALERT", val);
      }
    }
  },
  watch: {
    alert(val) {
      let vm = this;
      if (val) {
        Notify({
          type: "warning",
          message: val,
          onClose: function() {
            vm.alert = "";
          }
        });
      }
    }
  }
};
</script>
<style lang="scss" src="@/assets/css/default.scss"></style>
